      p Todo plan de acción debe contener descritos y especificados los siguientes aspectos fundamentales:

      .col-md-11
        .p • Análisis: diagnóstico de la situación y de las necesidades que se busca atender o mejorar.
        .p • Objetivo: el objetivo del plan de acción debe ser claro, concreto y medible, permitiendo identificar las estrategias y orientar la ejecución hacia los resultados esperados. Los planes de acción son útiles para coordinar y comprometer a personas y organizaciones en torno a metas comunes.
        .p • Actividades e itinerarios: descripción de las acciones, tareas y estrategias a ejecutar; incluye la ruta o camino, los lugares de paso, las actividades, servicios, tiempos, fechas de inicio y finalización.
        .p • Responsabilidades: asignación de tareas y roles a quienes participan en la elaboración y ejecución del plan.
        .p • Recursos: identificación de los recursos humanos, físicos y financieros necesarios para el cumplimiento de las actividades.
        .p • Plazos: establecimiento de la duración definida del plan, con inicio y término precisos.
        .p • Indicadores: parámetros de medición del logro de metas, relacionados con la cantidad, tiempo y calidad. Los indicadores de gestión permiten evaluar resultados, justificar acciones y tomar decisiones.
        .p • Ajustes: modificaciones o correcciones necesarias durante el desarrollo del proceso, con base en la retroalimentación y observación continua.

